import {profileDataRange} from "./input_val_range"
const LIMIT=profileDataRange
export default function getEmptyProfile(){
    return {
        "Name": "...",
        "Pset": 1,
        "Ver": "1.0.0",
        "Sequence": {
            // 高级设置的全局参数
            "GlobalParams":{
                "AutoPowerOn": true,
                "AutoPowerOff": false,
                // 默认方向
                "Direction": "CW",
                // 采样频率
                "SampleFrequency" :LIMIT.hightSetting.DefaultSampleFrequency,//hz
                // 采样点数
                "SamplingNumber" :LIMIT.hightSetting.DefaultSampleNum,
                // 开启标定
                "CalibrationeOn" : true,//
                // 是否开启全局角度监控
                "GlobalAngleMonitoring" : true,//全局角度监控
                "ThresholdValue" : LIMIT.hightSetting.DefaultStartTorque,//程序开始扭矩
                "AngleLimit": {
                    "Min": LIMIT.hightSetting.MinAngle,
                    "Max": LIMIT.hightSetting.MaxAngle
                },
            },
            "Branchs": [
                {
                    "Name": "MainBranch",
                    "Axis": {
                        "Name": "MainSpindle",
                        "StartMode": "csv",
                        "Direction": "CW"},
                    "Start": 0,
                    "End": 1,
                    //存放工步
                    "SeqItems": []
                }
            ]
        }
    }
}