<template>
    <div class="data-box">
        <div class="tech-basemsg-line">
            <span class="tech-num">{{ $t("tchnlgy.prcsid") }}:</span>
            <div v-if="curPsetId != -1" class="tech-select">
                {{ curPsetId.toString().padStart(3, "0") }}
            </div>
            <span class="tech-name">{{ $t("tchnlgy.prcsname") }}</span>
            <InputText :module="module" v-if="curPsetId != -1" class="tech-input" :disabled="true" :text="file.Name"
                @update:modelValue="file.Name = $event.text" width="24vw" :textValid="nameReason == ''"
                :validReason="nameReason">
            </InputText>
            <svg-loader v-if="curPsetId != -1" @click="showGlobalConfig" class="tech-setting" icon="StaticSetting"
                size="2.5rem"></svg-loader>
        </div>
        <div class="form-box">
            <FormItem class="pro-text" :label="$t('tchnlgy.timeOut')">
                <InputVal :module="module" :minNum="2000" uname="time"
                    v-model:sensorValue="NewStandardModeData.ActionPara.Timeout_mS">
                </InputVal>
            </FormItem>
        </div>
        <div class="inner-box">
            <svg-loader class="standard-mode-icon" icon='StandardMode' size="30rem"></svg-loader>
            <div class="form-item torque-unit">
                {{ $t('monitor.torque') }}/{{ sensorUnit('torque') }}
            </div>
            <div class="form-item angle-unit">
                {{ $t('monitor.angle') }}/{{ sensorUnit('angle') }}
            </div>
            <div class="form-item speed-unit">
                {{ $t('monitor.speed') }}/{{ sensorUnit('angle_speed') }}
            </div>
            <div class="form-item zero">
                <span>0.0</span>
            </div>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item max-angle-text"
                :label="$t('selftapping.maxtorque')">
                <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                    :showUnitName="false" :min-num="NewStandardModeData.ActionPara.Target"
                    :maxNum="this.$store.state.g_status.sensor_max * 1.2"
                    v-model:sensorValue="NewStandardModeData.ActionPara.ResultTorque.Max"></InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item target-angle-text"
                :label="$t('strategy.targetTorque')">
                <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                    :showUnitName="false" :min-num="NewStandardModeData.ActionPara.ResultTorque.Min"
                    :max-num="NewStandardModeData.ActionPara.ResultTorque.Max"
                    v-model:sensorValue="NewStandardModeData.ActionPara.Target"></InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item min-angle-text"
                :label="$t('selftapping.mintorque')">
                <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                    :showUnitName="false" :max-num="NewStandardModeData.ActionPara.Target"
                    v-model:sensorValue="NewStandardModeData.ActionPara.ResultTorque.Min"></InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item torque-threshold-text"
                :label="$t('release.torqueThreshold')">
                <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="torque"
                    :showUnitName="false" v-model:sensorValue="NewStandardModeData.ActionPara.ThresholdValue"></InputVal>
            </FormItem>

            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item min-angle"
                :label="$t('tighten.miniAngle')">
                <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="angle"
                    :showUnitName="false" :max-num="NewStandardModeData.ActionPara.ResultAngle.Max"
                    v-model:sensorValue="NewStandardModeData.ActionPara.ResultAngle.Min"></InputVal>
            </FormItem>
            <FormItem labelSize="1.25rem" leftDis="0" labelWidth="5rem" class="form-item max-angle"
                :label="$t('tighten.maxAngle')">
                <InputVal :module="module" height="2.4rem" fontSize="1.25rem" width="6.4rem" uname="angle"
                    :showUnitName="false" :min-num="NewStandardModeData.ActionPara.ResultAngle.Min"
                    v-model:sensorValue="NewStandardModeData.ActionPara.ResultAngle.Max"></InputVal>
            </FormItem>
            <FormItemTwo flexDir="row" class="vOne">
                <InputVal :module="module" fontSize="1.25rem" height="2.4rem" width="6.4rem" uname="angle_speed"
                    :show-unit-name="false" v-model:sensorValue="NewStandardModeData.ActionPara.Velocity">
                </InputVal>
                <span class="v-text">V1</span>
            </FormItemTwo>
            <FormItemTwo flexDir="row" class="vTwo">
                <InputVal :module="module" fontSize="1.25rem" height="2.4rem" width="6.4rem" uname="angle_speed"
                    :show-unit-name="false" v-model:sensorValue="NewStandardModeData.ActionPara.TightenVelocity">
                </InputVal>
                <span class="v-text">V2</span>
            </FormItemTwo>
        </div>
        <div class="btn-box">
            <LeetxButton  :enablerun="false" :module="module" class="confirm-box" textColor="#fff" borderRadius="4px" bgcColor="#00c155"
                fontSize="2.25rem" fontFamily="--SHSC-B" :text="$t('dbgdmrct.save')" @click="submitStandardData">
            </LeetxButton>
        </div>



    </div>
</template>
<script>
import InputVal from '@/components/input/InputVal.vue'
import { deepCopy } from '@/utils/deepCopy'
import LeetxButton from '@/components/controls/LeetxButton.vue'
import StandardMode from '@/components/profiles/Standard.js'
import InputText from '@/components/input/InputText.vue'
// import LeetxSwitch from '@/components/switch/LeetxSwitch.vue'
import { pfModApi, curPfPsetApi, curPfListApi } from "@/api/profile/profile";
import { messageApi } from "@/components/message/message";
import getEmptyProfile from '@/utils/empty_profile'
import { fillSeqItemsWithDatalog } from "@/components/profiles/profileHelper";
import cnfrmWin from '@/dialogs/common/confirm/cnfrm.js'
import { standardConfigDialog } from "@/dialogs/profile/standardConfig";

export default {
    components: {
        InputVal,
        LeetxButton,
        InputText
    },

    data() {
        return {
            module: "PROFILE",
            NewStandardModeData: StandardMode(),
            curPsetId: -1,
            file: getEmptyProfile(),
            //工艺文件列表
            profileNameLst: [],
            // 退出界面检查标志
            need_check_edit: false,
            global_name_json: "",
            global_paras_json: "",
            //高级设置
            global_advanced_paras_json: "",
        }
    },
    methods: {
        //  简单工艺高级设置
        showGlobalConfig() {
            let GlobalParams = this.file.Sequence.GlobalParams;
            standardConfigDialog({ GlobalParams })
                .then((res) => {
                    this.file.Sequence.GlobalParams = res
                    this.need_check_edit = true;
                })
                .catch((err) => {
                    err && console.log(err);
                });
        },
        // 获取文件列表
        getPfList(success = undefined) {
            curPfListApi()
                .then((result) => {
                    if (result.status == 9127) {
                        return;
                    }
                    if (result.status != 0) {
                        return;
                    }
                    this.NameLst = [];
                    this.profileNameLst = []
                    result.data.forEach((file) => {
                        if (file.pset != this.curPsetId)
                            this.profileNameLst.push(file.name);
                    });
                    success && success();
                })
                .catch(() => { });
        },

        sensorUnit(name) {
            return this.$store.state.sensors[name].name
        },
        excuteDefaultAction() {
            this.getPfList();
            this.curPsetId = this.$route.query.pset;
            if (this.curPsetId === undefined) return
            curPfPsetApi(this.curPsetId)
                .then((result) => {
                    this.need_check_edit = true;
                    //从接口获取文件
                    this.file = result.data.Profile
                    this.global_name_json = JSON.stringify(result.data.Profile.Name)
                    this.global_advanced_paras_json = JSON.stringify(this.file.Sequence.GlobalParams);
                    result.data.Profile.Sequence.Branchs[0].SeqItems.forEach(x => {
                        if (x.Action == "Standard") {
                            //简单工步数据
                            this.NewStandardModeData = deepCopy(x);
                            this.global_paras_json = JSON.stringify(this.NewStandardModeData)
                        }
                    });
                })
                .catch((err) => {
                    console.log("getFileContent  error ", err);
                });
        },
        async submitStandardData() {
            if (this.nameReason != "" && this.curPsetId != -1) {
                messageApi.warning([this.$t("job.invalidName")]);
                return;
            }
            if (!this.enabledEdit) {
                return messageApi.warning(["该文件处于运行状态", "不允许修改"]);
            }
            this.file.Sequence.Branchs[0].SeqItems = fillSeqItemsWithDatalog([this.NewStandardModeData]);
            this.need_check_edit = false;
            try {
                await pfModApi({ "Profile": this.file })
                this.need_check_edit = false;
                messageApi.success([this.$t('setop.saveSuc')])
                this.excuteDefaultAction()
            } catch (err) {
                console.log("pfModApi  error ", err);
                messageApi.error([`工艺文件保存失败`]);
            }
        },
        isChanged() {
            //简单工艺内容
            let global_paras_unchanged = this.global_paras_json == JSON.stringify(this.NewStandardModeData)
            //name
            let global_name_unchanged = this.global_name_json == JSON.stringify(this.file.Name)
            //高级设置
            let global_advanced_unchanged = this.global_advanced_paras_json == JSON.stringify(this.file.Sequence.GlobalParams)
            if (global_paras_unchanged && global_name_unchanged && global_advanced_unchanged || this.need_check_edit == false) {
                return false
            } else {
                return true
            }
        },
        openDialog() {
            let title = this.$t("tchnlgy.savePset");
            let confirmText = this.$t("tchnlgy.yes");
            let cancelText = this.$t("tchnlgy.no");
            let msg = this.$t("tchnlgy.isSavePset");
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(() => {
                this.submitStandardData()
                this.excuteDefaultAction();
            }).catch(() => {
                this.excuteDefaultAction();
            });
        }

    },
    props: {
        stepData: {
            type: Object,
            default: function () {
                return {
                    startAngle: 0,
                    endAngle: 0,
                    maxTorque: 0,
                    minTorque: 0,
                    leftTorque: 0,
                    rightTorque: 0,
                    triggerTorque: 0,
                    getValWay: 0,
                    prefabTorqueMode: 0,
                    angleClear: 0,
                    torqueClear: 0,
                }
            }
        }
    },
    beforeRouteLeave(to, from, next) {
        if (this.nameReason != "" && this.curPsetId != -1) {
            messageApi.warning([this.$t("job.invalidName")]);
            next(false);
            return;
        }
        if (!this.isChanged()) {
            next()
        } else {
            let title = this.$t("tchnlgy.savePset");
            let confirmText = this.$t("tchnlgy.yes");
            let cancelText = this.$t("tchnlgy.no");
            let msg = this.$t("tchnlgy.isSavePset");
            cnfrmWin({
                title,
                confirmText,
                cancelText,
                msg
            }).then(async () => {
                await this.submitStandardData()
                next()
            }).catch(() => {
                next()
            });
        }

    },
    watch: {
        "$route.query.pset": {
            immediate: true,
            handler: function (to) {
                if (to != undefined) {
                    if (this.isChanged()) {
                        this.openDialog()
                    } else {
                        // this.getPfList();
                        this.excuteDefaultAction();
                    }
                }
            },
        },
    },
    created() {
        this.getPfList();
    },
    computed: {
        nameReason() {
            // 没有正在编辑的文件,文件名可以为空
            if (this.curPsetId == -1) return -1;
            // 1代表文件名为空,2代表工艺名称重复,-1代表合法的工艺文件名
            if (this.file.Name.length == 0) {
                return this.$t('dlgprogram.nameNotEmpty')
            }
            if (
                this.file.Name && this.profileNameLst.length > 0 &&
                this.profileNameLst.indexOf(this.file.Name) != -1
            ) {
                return this.$t('dlgprogram.nameRepeat')
            }
            return "";
        },
        // enabled() {
        //     if (
        //         "PROFILE" in this.$store.state.f_status.permissions &&
        //         this.$store.state.f_status.permissions["PROFILE"] == 4
        //     )
        //         return true;
        //     else return false;
        // },
        // pointerEvent() {
        //     return this.enabled ? "auto" : "none";
        // },
        // opacity() {
        //     return this.enabled ? 1 : 0.5;
        // },
        enabledEdit() {
            return (
                (this.curPsetId == this.$store.state.g_status.pset &&
                    this.$store.state.g_status.busy) == false
            );
        },
    }

}
</script>
